#presentation{
    margin: 50px 100px 0 50px;
    font-size: 1.3rem;

    p{
        text-indent: 50px;
    }

    .quote-right {
        float: right; /* Place l'image à droite */
      }

      &::after {
          content: "";
          display: table;
          clear: both; /* Nettoie le flottement pour que le texte suivant ne soit pas affecté */
        }
}
